import { atom } from 'jotai';
import { ToolResult } from '@/common/types';

/**
 * Atom for storing tool results for each session
 * Key is the session ID, value is an array of tool results for that session
 */
export const toolResultsAtom = atom<Record<string, ToolResult[]>>({});

/**
 * Map to track tool calls to their results (not an atom, just a cache)
 */
export const toolCallResultMap = new Map<string, ToolResult>();

/**
 * Interface for streaming tool call state
 */
export interface StreamingToolCall {
  toolCallId: string;
  toolName: string;
  accumulatedArguments: string;
  messageId?: string;
  startTime: number;
}

/**
 * Atom for storing active streaming tool calls
 * Key is toolCallId, value is the streaming state
 */
export const streamingToolCallsAtom = atom<Record<string, StreamingToolCall>>({});
